<template>
  <div>
    <el-table :data="tableData" stripe style="width: 100%">
      <el-table-column fixed prop="Cluster" label="Cluster"> </el-table-column>
      <el-table-column fixed prop="MACAddress" label="MACAddress"> </el-table-column>
      <el-table-column fixed prop="IP" label="IP"> </el-table-column>
      <el-table-column fixed prop="DeviceOwner" label="DeviceOwner"> </el-table-column>
      <el-table-column fixed prop="DeviceID" label="DeviceID"> </el-table-column>
    </el-table>
  </div>
</template>

<script>
// import channelServ from '@/api/channel'
import networks from "@/api/networks";
export default {
  data() {
    return {
      json_fields: {
        Cluster: "Cluster",
        MACAddress: "MACAddress",
        IP: "IP",
        DeviceOwner: "DeviceOwner",
        DeviceID: "DeviceID",
      },
      tableData: [],
    };
  },
  methods: {
    async getDataMethod(e) {
      let qs = {
        ip: e.ip,
      };
      new Promise((resolve, reject) => {
        networks
          .getData(qs)
          .then((res) => {
            this.tableData = [];
            console.log(res.data.data);
            for (var data of res.data.data.ports) {
              this.tableData.push({
                Cluster: data.cluster_name,
                MACAddress: data.mac_address,
                IP: data.ip,
                DeviceOwner: data.device_owner,
                DeviceID: data.device_id,
              });
            }
            console.log(this.tableData)
            this.download = "block";
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
}
</script>

