<template>
  <el-form :inline="true" :model="formInline" >
    <el-form-item label="IP:">
      <el-input v-model="formInline.ip" clearable placeholder="请输入IP"></el-input>
    </el-form-item>
    
    <el-form-item>
      <el-button type="primary" @click="onSubmit">查询</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
    data() {
        return {
            formInline: {
                ip: '',
            },
        }
    },
    methods: {
    onSubmit: function() {
      if (this.formInline.ip === '') {
        return
      }
      this.$emit("onSubmit",this.formInline);
    },
    }
}
</script>

